.venue-section {
	position: relative;
	padding: 5rem 0;

	@include max-md {
		padding: 3rem 0;
	}

	.orn-1 {
		position: absolute;
		top: 2rem;
		right: 0;
		width: 12%;
		transform: translate(50%, 0) rotate(-15deg);
		@include max-lg {
			width: 25%;
		}
		@include max-sm {
			width: 30%;
		}
	}

	.orn-2 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		transform: translate(-66%, -10%) rotate(15deg);
	}

	.orn-3 {
		position: absolute;
		top: 3rem;
		right: 0;
		width: 90%;
		transform: translate(50%, 0) rotate(-15deg);
	}

	.orn-4 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 120%;
		transform: translate(-40%, 20%) rotate(-15deg);
		opacity: 0.1;
	}

	.orn-5 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 50%;
		transform: translate(-50%, 40%) rotate(-15deg) scaleX(-1);
		opacity: 0.1;
		@include max-lg {
			width: 80%;
		}
		@include max-sm {
			width: 100%;
		}
	}

	.venue-wrapper {
		position: relative;
		z-index: 3;

		.venue-description {
			position: relative;
			margin: 0 auto 5rem;
			z-index: 2;
			text-align: left;

			@include max-lg {
				margin-bottom: 3rem;
			}

			.title-section {
				text-align: left;
			}
		}

		.venue-content {
			position: relative;

			.ribbon-venue {
				position: relative;
				margin-bottom: 1rem;

				svg {
					width: 40px;
					height: auto;

					path {
						stroke: var(--color-dark);
					}
				}

				.st0 {
					fill: var(--color-secondary);
				}

				.st1 {
					fill: none;
					stroke: var(--color-secondary);
					stroke-width: 19;
					stroke-linecap: round;
					stroke-miterlimit: 10;
					transform-origin: 50% 75%;
				}
			}

			.card {
				background-color: var(--color-primary);
				color: var(--color-dark);
				text-align: left;
				border: none;

				.card-body {
					position: relative;
					padding: 2rem;

					@include max-md {
						padding: 1rem;
					}

					h4 {
						padding: 0 0 1rem;
					}

					.event-description {
						position: relative;
						padding: 1rem 0;
						margin-bottom: 1rem;

						p {
							margin-bottom: 0;
						}
					}

					.event-name {
						position: relative;
						width: 100%;
						margin-bottom: 1rem;

						&-body {
							position: relative;
							padding: 4rem 3rem;
						}

						.date {
							position: relative;
							width: 100%;
							padding: 0.5rem 0 1rem;

							h5 {
								margin-bottom: 0;
								font-size: 36px;
							}
						}

						h4 {
							font-family: $font-secondary;
						}

						p {
							margin-bottom: 0;
							font-weight: 600;
						}
					}

					.event-place {
						position: relative;

						.widget-elements {
							width: 100%;
							text-align: center;
						}
					}
				}
			}
		}
	}

	.prokes-wrapper {
		margin: 3rem 0 0;
		position: relative;
		padding: 2rem;
		text-align: center;
		z-index: 2;

		.title-section {
			&::before {
				all: unset;
			}
		}

		@include max-md {
			padding: 2rem 0;
		}

		* {
			position: relative;
		}
	}

	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;

		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;

			@include max-md {
				width: 120px;
			}

			@include max-xs {
				width: 45%;
			}
		}
	}

	.row-venue {
		row-gap: 3rem;
	}

	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}

		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}

		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}

		40% {
			opacity: 1;
		}

		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}

		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}

		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}

		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}

		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
