.gallery-section {
	position: relative;
	padding: 5rem 0;

	@include max-md {
		padding: 3rem 0;
	}

	.img-gallery {
		height: auto;
		width: 100%;
		aspect-ratio: 3/4;
		object-fit: cover;
		margin-bottom: 1rem;
	}

	.splide {
		&__pagination {
			bottom: -0.5em;

			&__page.is-active {
				background-color: var(--color-secondary);
			}
		}

		.splide__arrow {
			border-radius: 0;
			width: 65px;
			height: 65px;
			background-color: var(--color-secondary);
			transform: translateY(-62%);

			@include max-lg {
				width: 50px;
				height: 50px;
			}

			svg {
				fill: var(--text-button);
				width: 25px;
				height: 30px;

				@include max-md {
					width: 20px;
					height: 25px;
				}
			}

			&--prev {
				left: 0;
			}

			&--next {
				right: 0;
			}
		}
	}
}